:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-light: hsl(230, 19%, 15%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(227, 50%, 90%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

@media (prefers-color-scheme: light) {
  :root {
    --background: var(--light-bg);
    --text-color1: var(--light-text2);
    --text-color2: var(--light-text1);
    --card-background: var(--light-card);
    --card-hover: var(--light-card-hover);
    --toggle: var(--toggle-light);
    --toggle-background: var(--toggle-bg-light);
    --toggle-button: var(--light-text2) ;
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color1: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-background: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-background: linear-gradient(255deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
    --toggle-button: var(--light-text2) ;
  }
}
body.light {
  --background: var(--light-bg);
  --text-color1: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-background: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-background: var(--toggle-bg-light);
  --toggle-button: var(--light-text2) ;
}

body.dark {
  --background: var(--dark-bg);
  --text-color1: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-background: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-background: linear-gradient(255deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  --toggle-button: var(--light-text2) ;
}

:root {
  --font-inter: "Inter", sans-serif;
  --font-nunito: "Nunito", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--background);
  color: var(--text-color1);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media screen and (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.875rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media screen and (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media screen and (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media screen and (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.header__toggle {
  margin-top: 1.25rem;
}
@media screen and (min-width: 40em) {
  .header__toggle {
    margin-top: 0;
  }
}

.toggle {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3rem;
  justify-content: space-between;
  align-items: center;
  border: none;
  padding: 0;
}
@media screen and (min-width: 40em) {
  .toggle {
    width: auto;
    gap: 0.625rem;
  }
}
.toggle label {
  color: var(--text-color2);
  font-size: 0.875rem;
  font-weight: 700;
  align-self: center;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  position: relative;
  height: 1.5rem;
  width: 1.5rem;
  cursor: pointer;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid var(--toggle);
}
.toggle__background {
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  border-radius: 0.75rem;
  overflow: hidden;
  background: var(--toggle-background);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  display: block;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  height: 1.125rem;
  width: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 0.1875rem;
}

.card-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media screen and (min-width: 40em) {
  .card-wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 71.875em) {
  .card-wrapper {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  width: 100%;
  background: var(--card-background);
  color: var(--text-color2);
  text-align: center;
  padding: 1.5625rem;
  margin: auto;
  border-radius: 0.3125rem;
  transition: all 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 5px solid var(--facebook);
}
.card--twitter {
  border-top: 5px solid var(--twitter);
}
.card--instagram {
  position: relative;
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(255deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.card--youtube {
  border-top: 5px solid var(--youTube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color1);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--text-color2);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.3125rem;
}
.card-grid .card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}